import $ from 'jquery';

export default class {
	constructor(options) {
		const defaults = {
			el: '.js-body',
			dom: {
				item: '.js-item'
			}
		};

		this.options = $.extend(true, {}, defaults, options);
		this.$el = $(this.options.el);
		this.$document = $(document);
		return this;
	}

	getDom() {
		this.$item = this.$el.find(this.options.dom.item);
		return this;
	}

	onInput(e) {
		const $target = $(e.target);
		const $el = $target.parent();
		const value = $target.val();
		if (value.length < 1) {
			console.log('zero');
			$target.remove();
			// $el.find('input').last().focus();
			let last = $el.find('input').last();
			console.log(last);
			// last.on('focus', this.onFocus.bind(this));
			last.focus();
		}
		else if (value.length >= 46 ) {
			$el.append("<input type='text' class='item js-item input-text'>");
			// $el.find('input').last().focus();
			// console.log($el.children().length);
		}
		else {
			console.log('not zero');	
		}
		// console.log(value.length);	
	}

	onFocus(e) {
		const $target = $(e.target);
		const input = $target.get(0);
		const value = input.value;
		console.log(value);		
		setTimeout(function(){
			if ($target.setSelectionRange) {
				$target.setSelectionRange(2,3);
				console.log('qweewq');
			}
			else if ($target.createTextRange) {
				let range = $target.createTextRange;
				console.log(range);
				$target.createTextRange.collapse(true);
				$target.createTextRange.moveEnd('character', value.length);
				$target.createTextRange.moveStart('character', value.length);
				$target.createTextRange.select();
			}
		}, 10);
		$target.setSelectionRange(value, value);
	}

	render() {
		if (this.$el.length) {
			this.getDom();
			this.$item.on('input', this.onInput.bind(this));
			this.$item.on('focus', this.onFocus.bind(this));
		}
		return this;
	}
}


